a {
  text-decoration: none;
}

.app-wrapper {
  height: 100vh;
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-areas:
      'header header header'
      'nav content content';
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  /*grid-gap: 10px;*/
}

.app-wrapper-content {
  grid-area: content;
  background: #0068af;
  /*padding: 10px;*/
}

body {
  background: #0093f7;
}

